.CreateStocks {
  width: 95%;
  background-color: var(--stock-color);

  position: relative;
  border-radius: 10px;
  box-shadow: var(--shadow-small);
  margin: 10px auto 10px auto;
  padding: 20px;

  > li:first-child {
    display: flex;
    justify-content: space-between;
    padding: 0 10px;

    > div {
      display: flex;
      align-items: center;
      box-shadow: none;
      padding: 0;
      margin: 0;
      background-color: var(--stock-color);
      > img {
        width: 30px;
        height: 30px;
      }
      > h2 {
        margin-left: 5px;
        font-size: var(--medium-font-size);
      }
    }
    > button {
      img {
        vertical-align: middle;
        width: 20px;
        height: 20px;
        &:hover {
          transform: rotate(180deg);
          transition: 0.7s;
        }
      }
    }
  }
  .stockItem {
    background-color: #fff;
    border-radius: 10px;
    padding: 10px;
    box-shadow: var(--shadow-small);
    background-color: #fff;
    margin: 15px 10px 0 10px;
    > h3 {
      display: flex;
      align-items: center;
      padding-bottom: 5px;
      justify-content: space-between;

      > p {
        font-size: var(--medium-font-size);
        font-weight: var(--medium-font-weight);
      }
      > .itemDeleteButton {
        width: 20px;
        height: 20px;
        border-radius: 5px;
        margin-right: 10px;
        color: #fff;
        background-color: #bdbdbd;
      }
    }

    > p {
      padding-bottom: 10px;
      > em {
        font-size: var(--medium-font-size);
        font-style: normal;
      }
      img {
        width: 10px;
        height: 10px;
        margin-left: 2%;
      }
      > span {
        font-size: var(--small-font-size);
        padding-left: 2px;
        &:last-child {
          margin-left: 2%;
        }
      }
    }
    > time {
      font-size: 0.8rem;
    }
  }
}
